<template>
  <div>
    <!-- 设置返回按钮 START-->
    <header>
      <span>作品圈</span>
      <a v-if="$store.state.user"><img src="../../../static/images/图层 40@2x.png" alt="照相机" @click="uploadWorkCircle"></a>
    </header>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <login v-if="showLoginModal" v-on:showDialogEvent="showDialogEvent"></login>
    </transition>
    <!--设置返回按钮 END-->
    <div class="creation-container">
      <section class="creation_item" v-for="(work, index) in worksList" :key="index">
        <div class="creation_header">
          <div>
            <a><img :src="work.userAvatar.indexOf('http') == -1 ? productURL + work.userAvatar : work.userAvatar" alt="作品详情作者头像" @click="enterPerCenter(work)"></a>
          </div>
          <div>
            <a>{{work.nickName}}</a>
            <br><span>{{new Date(work.dateAdded) | dateDiff}}</span>
          </div>
          <div>
            <a v-if="work.userId != account" @click="changeAttentionStatus(work)" :class="work.isFollow == 'Y' ? 'followed' : ''">{{work.isFollow == 'Y' ? '已关注' : '关注'}}</a>
          </div>
        </div>
        <div class="creation_item_content" @click="__enterWorkCircleDetail(work)">
          <p class="work_content">{{work.content}}</p>
          <div class="creation_item_row">
            <img v-for="(workImg, index_) in work.contentPics.split(',')" :key="index_" :src="workImg" alt="作品图片">
          </div>
          <div v-if="work.contentPics && work.contentPics.split(',').length > 6" class="workWask">{{work.contentPics.split(',').length}}+</div>
        </div>
        <div class="creation_read_num">阅读{{work.browseCount}}</div>
        <div class="creation_footer">
          <div @click="changePraiseStatus(work)"><img :src="work.isPraise == 'Y' ? yesPraiseImg : noPraiseImg" alt="点赞"><span>{{work.numberFabulous}}</span></div>
          <div @click="__enterWorkCircleDetail(work)"><img :src="commentImg" alt="评论"><span>{{work.numberComments}}</span></div>
          <div><img :src="shareImg" alt="分享"><span>{{work.numberShare}}</span></div>
        </div>
      </section>
    </div>
    <loading v-if="showLoading" :title="loadingTitle"></loading>
    <transition enter-active-class="zoomIn" leave-active-class="zoomOut">
      <topic v-if="showTopic" class="animated" :title="topicTitle"></topic>
    </transition>
    <homeFooter></homeFooter>
  </div>
</template>
<script>
  import homeFooter from '@/components/homeFooter'
  import login from '@/components/login'
  export default {
    name: 'workCircle',
    components: {
      homeFooter,
      login
    },
    data() {
      return {
        worksList: new Array(),
        noPraiseImg: require('../../assets/img/jef4eada.png'),
        yesPraiseImg: require('../../assets/img/jef4f7vr.png'),
        commentImg: require('../../assets/img/jehyxybc.png'),
        shareImg: require('../../assets/img/jehyyijh.png'),
        falgNoData: false,
        page: 1,
        pagination: true,
        showData: false,
        pageYTop: 0,
        pageYBottom: 0,
        account: '',
        showLoginModal: 0
      }
    },
    created() {
      window.onscroll = () => {
        this.dropDown()
      }
      this.$Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      })
      this.axios.get('/api/getWorksList', {
        params: {
          page: this.page
        }
      }).then(data => {
        this.worksList = data.data.result.list
        this.account = data.data.account
        this.$Indicator.close()
      }).catch(err => {
        console.log(err)
      })
    },
    methods: {
      // 分页
      dropDown() {
        if (this.falgNoData) { //数据全部加载完了
          this.$Indicator.close()
          this.$Toast({
            message: '没有更多作品了',
            position: 'middle',
            duration: 1000
          })
          return
        } else if (this.scrollEvent() && this.pagination) { //如果满足触发条件，执行
          this.$Indicator.open({
            text: '加载中...',
            spinnerType: 'fading-circle'
          })
          this.page += 1
          this.axios.get('/api/getWorksList', {
            params: {
              page: this.page
            }
          }).then((res) => {
            if (this.pagination) {
              if (res.data.result.list.length > 0) {
                this.worksList = this.worksList.concat(res.data.result.list)
                this.$Indicator.close()
              } else {
                this.$Indicator.close()
                this.$Toast({
                  message: '没有更多作品了',
                  position: 'middle',
                  duration: 1000
                })
                this.pagination = false
                this.showData = true
              }
            }
          }).catch((err) => {
            console.log(err)
          })
        }
      },
      // 更改关注状态
      changeAttentionStatus(workInfo) {
        if(!this.$store.state.user){
          this.showLoginModal = 1
          return
        }
        if (workInfo.isFollow == 'N') {
          this.axios.post('/api/user-following/add', {
            followingId: workInfo.userId
          }).then(data => {
            if (data.data.result == '关注成功') {
              this.$Toast({
                message: '关注成功',
                position: 'middle',
                duration: 1000
              })
              this.worksList.forEach(element => {
                if (element.userId == workInfo.userId) {
                  element.isFollow = 'Y'
                }
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else if (workInfo.isFollow == 'Y') {
          this.axios.post('/api/user-following/delete', {
            followingId: workInfo.userId
          }).then(data => {
            if (data.data.result == '移除成功') {
              this.$Toast({
                message: '取消关注成功',
                position: 'middle',
                duration: 1000
              })
              this.worksList.forEach(element => {
                if (element.userId == workInfo.userId) {
                  element.isFollow = 'N'
                }
              })
            } else if (data.data.result == '移除失败') {
              this.$Toast({
                message: '取消关注失败',
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(err => {
            console.log(err)
          })
        }
      },
      // 对作品进行点赞/点赞以及取消赞
      changePraiseStatus(workInfo) {
        if(!this.$store.state.user){
          this.showLoginModal = 1
          return
        }
        this.axios.get('/api/likeWorks', {
          params: {
            worksId: workInfo.worksId,
            action: workInfo.isPraise == 'Y' ? 'NORMAL' : 'UPVOTE' // 判断当前作品的点赞状态
          }
        }).then(data => {
          if (data.data.status === 'success' && data.data.result == '取消点赞 -1！') {
            workInfo.isPraise = 'N'
            workInfo.numberFabulous = --workInfo.numberFabulous
            this.$Toast({
              message: '取消点赞成功',
              position: 'middle',
              duration: 1000
            })
          } else if (data.data.status === 'success' && data.data.result == '点赞 +1！') {
            workInfo.isPraise = 'Y'
            workInfo.numberFabulous = ++workInfo.numberFabulous
            this.$Toast({
              message: '点赞成功',
              position: 'middle',
              duration: 1000
            })
          }
        }).catch(err => {
          console.log(err)
        })
      },
      // 进入个人中心
      enterPerCenter(userInfo) {
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: userInfo.userId
          }
        })
      },
      // 进入作品上传
      uploadWorkCircle() {
        this.$router.push({
          name: 'publishWorkCircle',
          params: {
            id: 'none'
          }
        })
      },
      // 进入作品详情
      __enterWorkCircleDetail(info) {
        this.axios.get('/api/plusWorks', {
          params: {
            worksId: info.worksId,
            type: 'LL'
          }
        }).then(data => {
          this.$router.push({
            name: 'workCircleCommentList',
            params: {
              id: info.worksId
            }
          })
        }).catch(err => {
          console.log(err)
        })
      }
    }
  }
</script>


<style lang="less" scoped>
  body {
    background-color: #eee;
  }
  /* HEADER 返回按钮 START */
  header {
    width: 100%;
    background-color: #fff;
    text-align: center;
    height: 3.125rem;
    position: sticky;
    top: 0;
    z-index: 1;
  }
  header>a {
    vertical-align: middle;
    position: absolute;
    right: .625rem;
    top: 50%;
    transform: translateY(-50%);
  }
  header>a>img {
    width: 1.875rem;
    height: auto;
  }
  header>span {
    display: block;
    line-height: 3.125rem;
    height: 3.125rem;
  }
  /* HEADER 返回按钮 END */
  .creation-container {
    padding-bottom: 5rem;
    background-color: #fff;
  } // 作品的具体内容
  .creation_item {
    background-color: #fff;
    .creation_header {
      display: flex;
      height: 3.125rem;
      &>div:nth-child(1) {
        -webkit-box-flex: 2;
        flex: 2;
        text-align: center;
        a {
          display: inline-block;
          line-height: 3.125rem;
          img {
            width: 2.5rem;
            height: 2.5rem;
            vertical-align: middle;
          }
        }
      }
      &>div:nth-child(2) {
        -webkit-box-flex: 6;
        flex: 6;
        padding-top: .3125rem;
        span {
          color: #aaa;
        }
      }
      &>div:nth-child(3) {
        -webkit-box-flex: 2;
        flex: 2;
        a {
          display: block;
          width: 80%;
          height: 40%;
          border: 1px solid #aaa !important;
          margin-top: .9375rem;
          text-align: center;
          border-radius: .625rem;
          padding: .3125rem 0;
        }
      }
    } // 作品详情内容
    .creation_content {
      box-sizing: border-box;
      p {
        width: 100%;
        padding: .625rem 0 .625rem 1.25rem;
        box-sizing: border-box;
      }
      ul {
        box-sizing: border-box;
        width: 100%;
        padding: .625rem 1.25rem .625rem 1.25rem;
        li {
          margin: .3125rem 0;
          &>img {
            width: 100%;
          }
        }
      }
    }
    .creation_read_num {
      width: 100%;
      padding: .3125rem 0 .3125rem 1.25rem;
      box-sizing: border-box;
      color: #aaa;
    }
  }
  .creation_footer {
    background-color: #fff;
    display: flex;
    padding: .3125rem 0;
    box-sizing: border-box;
    div {
      flex: 1;
      text-align: center;
      img {
        vertical-align: middle;
        width: 1.25rem;
        height: 1.25rem;
      }
      span {
        vertical-align: middle;
        margin-left: .625rem;
      }
    }
  } // 底部评论和点赞 START
  footer {
    width: 100%;
  }
  .followed {
    color: #fff;
    background-color: #3fb140;
  }
  /* 作品内容 START */
  .creation_item_content {
    padding-left: 1.25rem;
    position: relative;
    &>.workWask {
      position: absolute;
      bottom: 0;
      right: .625rem;
      height: 1.5625rem;
      width: 3.125rem;
      background-color: rgba(0, 0, 0, .3);
      z-index: 1;
      color: #fff;
      text-align: center;
      line-height: 1.5625rem;
      border-radius: 1.25rem;
    }
  }
  .creation_item_row {
    width: 100%;
    img {
      width: 33%;
      height: 6.25rem;
    }
  }
  .work_content{
    padding: .3125rem 0;
    min-height: 1.25rem;
  }
</style>



